<template>
  <div id="base_login">
    <div class="table_container">
     <table>
      <tr>
        <th>
          <div id="picture_part">
            <img src="../assets/background/hardChair.png" alt="hardChair">
          </div>
        </th>
        <th>
          <!--model绑定数据，rules绑定规则，prop对应具体规则，ref属性绑定对象-->
          <el-form :model="loginForm"
                   :rules="rules"
                   ref="loginForm"
                   class="login_container"
                   label-position="left"
                   label-width="0px"
                   v-loading="loading">
            <h3 class="login_title">Login</h3>
            <!--<el-form-item prop="username" style="width: 200px;margin-left: 125px;margin-top: 20px;position: fixed;top:300px">
                <el-input type="text"
                          name="username"
                          aria-describedby="basic-addon1"
                          v-model="loginForm.username"
                          auto-complete="off"
                          placeholder="username"></el-input>
            </el-form-item>-->
            <el-form-item prop="username"  style="width: 200px;margin-left: 125px" >
              <el-input type="text" v-model="loginForm.username"     ref="test"
                        auto-complete="off" placeholder="username" id="name"   ></el-input>
            </el-form-item>
            <!--<el-form-item prop="password" style="width: 200px;margin-left: 125px;margin-top: 50px;position: fixed;top:330px">
                <el-input type="password"
                          name="password"
                          aria-describedby="basic-addon1"
                          v-model="loginForm.password"
                          auto-complete="off"
                          placeholder="password" style="border-color: TRANSPARENT"></el-input>
            </el-form-item>-->
            <el-form-item prop="password" style="width: 200px;margin-left: 125px" >
              <el-input type="password" v-model="loginForm.password"
                        auto-complete="off" placeholder="password"></el-input>
            </el-form-item>
            <el-form-item style="width: 100%;">
              <el-button type="button" class="btn btn-default"
                         style="width: 40%;background: #afb4db;border: none;margin-top: 0px;;margin-left: 35px"
                         v-on:click="login">login</el-button>
              <router-link to="register">
                <el-button type="button" class="btn btn-default"
                           style="width: 40%;background: #afb4db;border: none;margin-top: 0px;margin-left: 10px">register</el-button>
              </router-link>
              <!--测试表单数据绑定是否成功-->
              <!-- <el-button type="button" class="btn btn-default"
                         style="width: 40%;background: #afb4db;border: none;margin-top: 140px;margin-left: 10px" v-on:click="test">test</el-button>-->
            </el-form-item>
          </el-form>
        </th>
      </tr>
    </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      //loginForm对象，初始为空
      loginForm: {
        username: '',
        password: ''
      },
      //required判断是否为必填项，message提示信息，trigger触发事件（BLUR光标移动）
      rules: {
        username: [{required: true, message: '', trigger: 'blur'}],
        password: [{required: true, message: '', trigger: 'blur'}]
      },
      loading: false
    }
  },
  methods: {
    login () {
      //提交信息
      this.$axios.post('login', {
     // this.$axios.post('login', {
        username: this.loginForm.username,
        password: this.loginForm.password
      })
      //回调 校验通过,跳转到会议开设界面
        .then(resp => {
          if (resp.status === 200) {
          //if (resp.status === 200 && resp.data.hasOwnProperty("token")) {
          //this.$store.commit('login', resp.data);
          //this.$router.replace({path: '/'});
          //this.$router.replace({path: '/OpenMeeting'});
          //this.$router.push({ path:'http://localhost:80/#/OpenMeeting'});
            this.$router.push({ path:'OpenMeeting'});
            alert('登陆成功')
          } else{
            alert('登陆失败')
          }
        })
        //校验不通过
        .catch(error => {
          console.log(error);
          alert('登陆失败');
        })
    },
   /* 测试表单数据绑定是否成功
      test(){
      console.log(this.loginForm);
    }*/
  }
}
</script>

<style>
  #base_login{
    background-color: lightgray;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  body{
    background-color: lightgray;
  }
  img{
    border-radius: 15px;
    height: 400px;
    width: 650px;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .table_container{
    background-color: grey;
  }
  table, th, td {
    border: 0px solid blue;
  }
  table{
    margin-top: 100px;
    margin-left: 200px;
    margin-bottom: 200px;
  }
  .login_container{
    border-radius: 15px;

    height: 400px;
    width: 450px;
    background: #faf0e6;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login_title {
    margin: 50px auto 40px auto;
    font-family: "Nirmala UI Semilight";
    font-size: 50px;
    color: #494e8f;
    text-align: center;
  }

</style>




