<template >


  <div id="base_register"  >
    <el-form :model="registerForm" :rules="rules" class="register_container" label-position="left"
             label-width="0px" v-loading="loading" :ref="registerForm">
      <h3 class="register_title">Register</h3>


   <p> 用户名</p> <el-form-item prop="username"   >
          <el-input type="text" v-model="registerForm.username"     ref="test"
                    auto-complete="off" placeholder="username" id="name"   ></el-input>
        </el-form-item>

      <p  >密码</p>
      <el-form-item prop="password"  >
        <el-input type="password" v-model="registerForm.password"
                  auto-complete="off" placeholder="password"></el-input>
      </el-form-item>


      <p  >邮箱</p>
      <el-form-item prop="email"  >
        <el-input type="text" v-model="registerForm.email"
                  auto-complete="off" placeholder="email"></el-input>
      </el-form-item>


      <p  >所属单位</p>
      <el-form-item prop="unit"  >
        <el-input type="text" v-model="registerForm.unit"
                  auto-complete="off" placeholder="Fudan University"  ></el-input>
      </el-form-item>

      <p  >所属区域</p>
      <el-form-item prop="area"  >
        <el-input type="text" v-model="registerForm.area"
                  auto-complete="off" placeholder="China"></el-input>
      </el-form-item>


      <el-form-item style="width: 100% ;margin-top:20px">
        <el-button type="primary" style="width: 40%;background: #afb4db;border: none" v-on:click="register(registerForm)">register</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Register',

  data () {

    const dataValid = (rule, value, callback) => {

      //对于为空的表单进行检验
      if(!value || value === '') {

        return callback(new Error('Can\'t be empty '))

      }
      //对于输入的表单内容进行检验，用户名、密码不能一致
      if(this.registerForm.username===this.registerForm.password){
        return callback(new Error('用户名，密码不能一致'))
      }
      return callback()
    }
    return {
      registerForm: {
        username: '',
        password: '',
        email: '',
        unit: '',
        area: '',

      },
      rules: {
        // blur 失去鼠标焦点时触发验证
        username: [{required: true, message: '不能为空', trigger: 'blur'},
          { pattern:/^[a-zA-Z\_]{1,1}([a-zA-Z0-9]|[-_]){4,31}$/g, message: "请输入正确的用户名格式", trigger: "blur"},//正则表达式
          {validator: dataValid, trigger: 'blur'}],
        password: [{required: true, message: '不能为空', trigger: 'blur'},
          { pattern:/(?!^(\d+|[a-zA-Z]+|[-_]+)$)^[\w-_]{5,31}$/g, message: "请输入正确的密码格式", trigger: "blur"},//正则表达式
          {validator: dataValid, trigger: 'blur'}],
        email: [{required: true, message: '不能为空', trigger: 'blur'},
          { pattern:/^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g, message: "请输入正确的邮箱", trigger: "blur"}, //正则表达式验证是否为正确的邮箱
          {validator: dataValid, trigger: 'blur'}],
        unit: [{required: true, message: '不能为空', trigger: 'blur'}, {validator: dataValid, trigger: 'blur'}],
        area: [{required: true, message: '不能为空', trigger: 'blur'}, {validator: dataValid, trigger: 'blur'}],

      },
      loading: false
    }
  },
  methods: {
    userTypeChange() {
    },
    register (formName) {
      this.$refs[formName].validate(valid => {
        if(valid){
          this.$axios.post('register', {
              username: this.registerForm.username,
              password: this.registerForm.password,
              email: this.registerForm.email,
              unit: this.registerForm.unit,
              area: this.registerForm.area,
            }
          )
            .then(resp => {
              // 成功后端返回200status
              if(resp.status === 200) {
                // 跳转到会议开设界面
                alert('注册成功');
                this.$router.push({ path:'OpenMeeting'});
              }
              else if(resp.status === 400) {
                alert('注册失败')
              }
            })
            .catch(error => {
              console.log(error);
              alert('注册失败')
            })
        } else {
          alert('注册失败-错误的提交')
        }
      })
    }
  }



}
</script>

<style scoped>

  #base_register{
    background: url("../assets/background/register.jpg") repeat;
    background-position: center;
    height: 100%;
    width: 100%;
  }
  body{
    background-color: #ffe7e0;
  }
  .register_container{
    border-radius: 25px;
    background-clip: padding-box;
    margin: -20px auto;
    width: 450px;
    height: 670px;
    padding: 35px 35px 20px 35px;
    background: url("../assets/background/checkerboard-cross.png") repeat;
    border: 10px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .register_title{
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
</style>



