<template>
  <div id="base_application">
    <el-form :model="applicationForm"
             :rules="rules"
             class="login_container"
             label-position="left"
             label-width="0px"
             v-loading="loading">
      <h3 class="application_title" >会议开设</h3>
      <el-form-item prop="shortname">
        <p>会议简称：</p>
        <el-input type="text"
                  v-model="applicationForm.shortname"
                  auto-complete="off"
                  placeholder="如ICSE2020"
        ></el-input>
      </el-form-item>
      <el-form-item prop="fullname">
        <p>会议全称：</p>
        <el-input type="text"
                  v-model="applicationForm.fullname"
                  auto-complete="off"
                  placeholder="如The 42nd International Conference on Software Engineering"></el-input>
      </el-form-item>

      <el-form-item prop="time">
        <p>举办时间：</p>
        <el-date-picker size="small" v-model="applicationForm.time" type="date" placeholder="选择日期时间" value-format="yyyy-MM-dd">

        </el-date-picker>
      </el-form-item>
      <el-form-item prop="place">
        <p>举办地点：</p>
        <el-input type="text"
                  v-model="applicationForm.place"
                  auto-complete="off"
                  placeholder="举办地点"></el-input>
      </el-form-item>
      <el-form-item prop="deadline">
        <p>投稿截止日期：</p>
        <el-date-picker size="small" v-model="applicationForm.deadline" type="date" placeholder="选择日期时间" value-format="yyyy-MM-dd">

        </el-date-picker>
      </el-form-item>
      <el-form-item prop="releasetime">
        <p>评审结果发布日期：</p>
        <el-date-picker size="small" v-model="applicationForm.releasetime" type="date" placeholder="选择日期时间" value-format="yyyy-MM-dd">

        </el-date-picker>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary"
                   style="width: 40%;background: #007fdb;border: none"
                   v-on:click="openmeeting">开设会议</el-button>

      </el-form-item>
    </el-form>

  </div>

</template>
<!--会议简称、全称、举办时间、举办地点、投稿截止日期、评审结果发布日期、信息不完整或有问题提示修改-->
<script>
  export default {
    name: 'Open',
    data () {
      return {
        applicationForm: {
          shortname: '',
          fullname: '',
          time:'',
          place:'',
          deadline:'',
          releasetime:''

        },
        rules: {
          shortname: [{required: true, message: '', trigger: 'blur'}],
          fullname: [{required: true, message: '', trigger: 'blur'}],
          time: [{required: true, message: '', trigger: 'blur'}],
          place: [{required: true, message: '', trigger: 'blur'}],
          deadline: [{required: true, message: '', trigger: 'blur'}],
          releasetime: [{required: true, message: '', trigger: 'blur'}],
        },
        loading: false
      }
    },
    methods: {
      openmeeting () {
        this.$axios.post('openmeeting', {
          shortname: this.applicationForm.shortname,
          fullname: this.applicationForm.fullname,
          time:this.applicationForm.time,
          place:this.applicationForm.place,
          deadline:this.applicationForm.deadline,
          releasetime:this.applicationForm.releasetime
        })
          .then(resp => {
            // if (resp.status === 200 && resp.data.hasOwnProperty("token")) { 暂时先去掉token
            if (resp.status === 200) {
              //  暂时先去掉异步跳转
              //  this.$store.commit('apply', resp.data)
              //  this.$router.replace({path: '/'})
              alert("申请成功" );
              this.$router.push({ path:'openMeeting'});
            } else{
              alert('申请错误1')
            }
          })
          .catch(error => {
            console.log(error)
            alert('该会议已存在')
          })
      }
    }
  }
</script>


<style scoped>
  #base_application{
    background: url("../assets/background/596344a5e6cca.jpg") repeat;
    background-color: rgba(27, 109, 133, 0.77);

    height: 1000px;
    width: 100%;

  }
  body{
    margin:10px;
    padding: 10px;
  }
  .login_container{
    border-radius: 15px;
    background-clip: padding-box;

    margin:-20px auto;
    width: 550px;
    height: 880px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 10px #cac6c6;
  }
  .application_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #494e8f;

  }
</style>



